<template>
  <div class="videos flexcenter">
    <div class="videomodal" v-if="isModalSowed">
      <div class="shaddow"></div>
      <div class="videoscreen">
        <FlexBox direction="column">
          <div :class="'hideBtnPlace ' + position + 'HideBtn'">
            <div class="hideBtn" :onclick="hideModal"></div>
          </div>
          <video ref="video" controls :class="'videoPlay ' + position">
            <source :src="sourse" type="video/mp4" />
            <p>
              Ваш браузер не поддерживает HTML5 видео. Используйте ссылку на
              видео для доступа.
            </p>
          </video>
        </FlexBox>
      </div>
    </div>
    <h1>my videos:</h1>
    <FlexBox :gap="20">
      <VideoCard :onclick="() => showModal(1)" img="video0001" />
      <VideoCard :onclick="() => showModal(2)" img="video0002" />
      <VideoCard :onclick="() => showModal(3)" img="video0003" />
      <VideoCard :onclick="() => showModal(4)" img="video0004" />
    </FlexBox>
  </div>
</template>

<script setup lang="ts">
import { onMounted, ref, computed } from "vue";
import { useStore } from "vuex";
import VideoCard from "@/components/bricks/VideoCard.vue";
import FlexBox from "@/components/containers/FlexBox.vue";

const store = useStore();
const isModalSowed = ref(false);
const video = ref();
const sourse = ref<string>(
  "https://svoyapi.ru/kuldiaeva/video/vlada_kuldiaeva_0002"
);

const showModal = (n: number) => {
  sourse.value =
    "https://svoyapi.ru/kuldiaeva/video/vlada_kuldiaeva_000" + String(n);
  isModalSowed.value = true;
};

const hideModal = () => {
  isModalSowed.value = false;
};

const screenRatio = computed(() =>
  (store.state.Status.currWidth / store.state.Status.currHeight).toFixed(2)
);

const position = computed(() => {
  return +screenRatio.value > 0.587 ? "gorizontal" : "vertical";
});

onMounted(() => {
  setTimeout(() => {
    store.commit("Status/showHeader");
  }, 1000);
});
</script>

<style lang="scss" scoped>
.videos {
  height: 90vh;
  background-color: $black;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  z-index: 5;
}

.gorizontal {
  max-height: 95vh;
  width: auto;
}

.vertical {
  max-width: 90vw;
  height: auto;
}

h1 {
  color: white;
}
video {
  height: 90vh;
  border: 2px solid silver;
}
.videomodal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 3;
}
.shaddow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 4;
  opacity: 0.85;
  background-color: black;
}
.videoscreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hideBtn {
  height: 100%;
  min-width: 2vw;
  background-repeat: no-repeat;
  background-size: 100%, auto;
  background-image: url("~@/assets/img/svg/closeSilver.svg");
  cursor: pointer;
}
.hideBtn:hover {
  background-image: url("~@/assets/img/svg/closeWhite.svg");
}

.hideBtnPlace {
  width: 95vw;
  display: flex;
  justify-content: flex-end;
}
.gorizontalHideBtn {
  width: calc(100vh / 1.75);
  height: 5vh;
  display: flex;
  align-items: flex-end;
}
.gorizontalHideBtn .hideBtn {
  height: 4vh;
  min-width: 4vh;
}
.verticalHideBtn {
  width: 95vw;
  height: 5vw;
}
.gorizontalHideBtn .hideBtn {
  height: 4vw;
  min-width: 4vw;
}
</style>
